import { Card, CardContent, Container, Grid } from "@mui/material";
import Contactme from "../Component/Contactme/Contactme";
import { makeStyles } from "@mui/styles";
const useStyles = makeStyles(theme => ({
  header: {
    textAlign: "center",
  },
  card: {
    borderBottom: `10px solid #f00`,
    height: "calc(100vh - 134px)",
    display: "flex",
    justifyContent: "center",
    textAlign: "center",
    alignItems: "center",
  },
  CardContainer: {
    display: "flex",
    marginTop: "20px",
    flexWrap: "wrap",
    gap: "20px",
  },
}));
const About = () => {
  const classes = useStyles();
  return (
    <>
      <h2 className={classes.header}>Contact Me</h2>
      <Container>
        <Grid item component={Card} xs={12} md={12} className={classes.card}>
          <CardContent>
            <div className={classes.CardContainer}>
              <Contactme
                primary="#e6e6ff"
                secondary="#0000cb"
                text="Facebook"
              />
              <Contactme
                primary="#fde6ff"
                secondary="#bb00cb"
                text="Instagram"
              />
              <Contactme primary="#ffe6e6" secondary="#e70000" text="Mail" />
            </div>
          </CardContent>
        </Grid>
      </Container>
    </>
  );
};

export default About;
